<template>
    <div class="index-content sub-page-content" ref="content">
        <div class="header" ref="header">
            <div class="date"><span id="time" class="time"></span>{{currentime}}</div>
            <div class="date"><span id="time" class="time"></span></div>
            <div class="icon logo">{{title}}</div>
            <div class="right">
                <a href="javascript:void(0)" class="icon message dot">消息</a><!--右上角圆点加dot样式-->
                <span class="user">{{userName}}</span>
                <a  class="icon btn-quit" @click="logout">退出</a>
            </div>
	      </div>
        <div class="nav clearfix" ref="nav">
            <span>
                <router-link :to="{path:item.path}" v-for="(item,index) in menu" :key="index">{{item.name}}</router-link>
                <!-- <router-link :to="{path:'/newIndex/home'}">平台首页 </router-link>
                <router-link :to="{path:'/newIndex/assetDistribution'}">资产分布</router-link>
                <router-link :to="{path:'/newIndex/newDevice'}">资产列表</router-link>
                <router-link :to="{path:'/newIndex/newWarn'}">预警信息</router-link>
            
                <router-link :to="{path:'/newIndex/newAnalysis'}">算法接入</router-link>
                <router-link :to="{path:'/newIndex/newComparison'}">比对调度</router-link>
                <router-link :to="{path:'/newIndex/newReport'}">统计报表</router-link>
                <router-link :to="{path:'/newIndex/newFace'}">人像库管理</router-link> -->
                <!-- <router-link :to="{path:'/newIndex/newSystem'}">系统配置</router-link> -->
            </span>
        </div>
        <div class="home" :style="homeHeight">
		      <router-view></router-view>
            <!-- 中间e -->
        </div>
    </div>
</template>

<script>
export default {
  name: "index",
  data() {
    let vm = this;
    let userName = vm.$store.state.user.user.account
    return {
      title : vm.$store.state.title,
      currentime: "", //当前时间
      userName : userName,
      homeHeight: {},
      menu : this.$store.state.menu.menu
    };
  },

  mounted() {
    this.getTime();
    //this.getH();
  },
  watch: {
    $route: {
      handler: function(val, oldVal) {
        this.$nextTick(() => {
          //this.getH();
        });
      },
      // 深度观察监听
      deep: true
    }
  },
  methods: {
    getH() {
      let _h =
        this.$refs.content.offsetHeight -
        this.$refs.header.offsetHeight -
        this.$refs.nav.offsetHeight;
      this.homeHeight = {
        height: _h + "px"
      };
    },
    getTime() {
      setTimeout(() => {
        this.currentime = this.$util.formatDateByArg(
          new Date(),
          "yyyy-MM-dd hh:mm:ss"
        );
        this.getTime();
      }, 1000);
    },

    logout(){
      this.$store.dispatch("logout");
      this.$router.push("/login");
    }

  },
  computed: {},

  components: {}
};
</script>

<style lang="scss">
.index-content {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: Microsoft YaHei, Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 1.42858;
  color: #000;
  background: url(../../assets/img/login/bg.jpg) center 0 no-repeat #081121;
  min-width: 1260px;
  min-height: 100%;
}
</style>

<style scoped  lang="scss">
@import "../../assets/css/style1.css";
.nav {
  text-align: left;
}
.home {
  box-sizing: border-box;
  border: 1px solid;
}
.header .logo{
  color : #fff;
  font-size : 28px;
  font-weight: 500;
  background:none;
}
</style>




