<template>
    <div class="zzjggl">
      <div class="panel">
            <div class="base-search">
              <label>算法名称：</label>
              <span class="m-input">
                  <el-input class="w-200" v-model="searchForm.algName" placeholder="请输入关键字"></el-input>
              </span>
              <label>算法编码：</label>
              <span class="m-input">
                  <el-input class="w-200" v-model="searchForm.algCode" placeholder="请输入关键字"></el-input>
              </span>
              <a href="javascript:void(0)" class="btn-search" @click="search">查询</a>
              <a href="javascript:void(0)" class="btn-add" @click="add" v-if="permissionResource.hasPermission('CREATION')">新增</a>
              <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')">批量删除</a>
            </div>
            <div class="line"></div>           
            <div class="base-table">
                <table-grid :option="option" ref="analysisList">
                    <template slot-scope="row">
                      <!-- <a href="javascript:void(0)"  title="重启">
                        <i class="btn-icon icon-12"></i>
                      </a>
                      <a href="javascript:void(0)" title="配置">
                        <i class="btn-icon icon-13"></i>
                      </a> -->
                      <a href="javascript:void(0)" title="编辑" @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')" >
                        <i class="btn-icon icon-16"></i>
                      </a>
                      <a href="javascript:void(0)" title="删除" @click="deleteTab(row)" v-if="permissionResource.hasPermission('DELETION')">
                        <i class="btn-icon icon-17"></i>
                      </a>
                      <!-- <a href="javascript:void(0)" title="口令修改">
                        <i class="btn-icon icon-14"></i>
                      </a> -->
                    </template>
                </table-grid>
            </div>
        </div>
        <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
            <dialogEdit :pData='msgDialogInfo' @equit='equit'></dialogEdit>
        </msgDialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogEdit from "@/views/xinjiang/analysis/opt/analyAdd"; 

export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      searchForm:{
        algName: '',
        algCode: ''
      },
      dialogVisible:{
        msgDialog: false
      },
      msgDialogInfo:{

      },
      msgDialog: {
        title: "新增",
        dialogVisible: true,
        width: "620px",
        data: ""
      },
      option: {
        isShowTableNav: false,
        tableOption: {
          singleSelect: true,
          isShowSelect: true, 
          hasCustomColum: true,
          tableTitle: [
            { prop: "algName", label: "算法名称" },
            { prop: "algCode", label: "算法编码" },
            { prop: "algType", label: "类型" },
            { prop: "status", label: "状态" },
            { prop: "creator", label: "创建人" },
            {
              prop: "createTime",
              label: "创建时间",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.modifiedDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          opeColumnWidth: 220,
          customRes: true
        },
        customLabel: "操作",
        url: "/vcpalgaccess/alg/findByPage",
        type: "post",
        notRequestSever: false,
        param: {
          /*pageSize: 10,
          pageNum: 1,
          para: {
            algName: "人脸动态",
            algCode: "FACE"
          }*/
        }
      }
    };
  },
  methods: {
    deleteTab(row){ 
      this.$confirm('确定删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/vcpalgaccess/alg/delete`,{id: row.rowData.id}).then(response =>{
            if(response.data.resultCd == 0){
              this.$notify({
                title: '成功',
                message: '已成功删除!!',
                type: 'success'
              });
              this.equit();
            } 
          })
        });
    }, 
    add(){
      this.msgDialog = {
        title: "新增",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '新增'};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){
      this.msgDialog = {
        title: "编辑",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '编辑', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.$refs.analysisList.flushTableData();
      this.dialogVisible.msgDialog = false;
    },
    search(){
      //let param = {}; 
      if (this.searchForm) { 
        this.option.param = Object.assign({pageSize: 10, pageNum: 1,},{para: this.searchForm});
      }  
      this.$refs.analysisList.searchTableData(this.option.param);
    }
  },
  components: {
    "table-grid": tableGrid,
    dialogEdit,
    msgDialog
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page .t-head,.base-search{
  text-align:left;
}
</style>




