<template>
    	<div class="zzjggl">
      <div class="panel">
          <div class="base-search">
              <label>用户名称：</label>
              <span class="m-input">
                  <el-input class="w-200" placeholder="请输入关键字"></el-input>
              </span>
              <a href="javascript:void(0)" class="btn-search"  @click="search()">查询</a>      
          </div>
          <div class="line"></div>
          <div>
              <table-grid :option="option">
                  <template slot-scope="row">
                      <el-button type="success" @click="openAddOrEditView(row.rowData)" size="mini">编辑</el-button>
                      <el-button type="warning" @click="delEmployee(row.rowData)" size="mini">删除</el-button>
                  </template>
              </table-grid>
          </div>
      </div>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
export default {
    data() {
    return {
      more: false,
      option: {
        isShowTableNav: false,
        hasCustomColum: true,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          tableTitle: [
            { prop: "account", label: "ID" },
            { prop: "account", label: "用户名" },
            { prop: "enterpriseName", label: "方法" },
            { prop: "name", label: "参数" },
            { prop: "phone.number", label: "IP" },
            { prop: "phone.number", label: "耗时" },
            { prop: "phone.number", label: "创建人" },
            { prop: "phone.number", label: "创建时间" }
          ]
        },
        customLabel: "操作",
        // url: "/operation-log",
        type: "get",
        notRequestSever: false,
        param: {
          pageSize: 9,
          pageNum: 1
        }
      },
      value: ""
    };
  },
  methods: {
    moreSearch() {
      this.more = !this.more;
    },
    search(_data){
      this.searchList.forEach(e=>{
        e.active=false;
      })
      this.$set(this.searchList[_data],"active",true);
    }
  },
  components: {
    "table-grid": tableGrid
  }
}
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
}
.sub-page .left-menu li {
  text-align: left;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
</style>



