<template>
  <div class="faceDetail">
      <el-row>
          <el-col :span="8">
              <img :src="formData.imageUrl" style="width:150px;height:150px"/>
          </el-col>
          <el-col :span="16">
              <el-form>
                  <el-form-item label="姓名：">{{formData.name}}</el-form-item>
                  <el-form-item label="性别：">{{formData.gender}}</el-form-item>
                  <el-form-item label="人像库：">{{formData.repoId}}</el-form-item>
                  <el-form-item label="证件类型：">{{formData.credentialType}}</el-form-item>
                  <el-form-item label="证件号码：">{{formData.credentialNumber}}</el-form-item>
                  <el-form-item label="民族：">{{formData.nationality}}</el-form-item>
                  <el-form-item label="国籍：">{{formData.country}}</el-form-item>  
              </el-form>
          </el-col>
      </el-row>
  </div>
</template>

<script>
import {baseUrl} from "@/utils/global"
export default {
  components:{},
  props:{
      pData:{
          type: Object,
          default: () =>{}
      }
  },
  data(){
    return {
        formData:{
            name:'张三',
            ger: '男',
            id: 43365662223,
            han: '汉',
            d: '高危'

        },
        constant: {lib: []}
    }
  },
  watch:{},
  computed:{},
  methods:{},
  created(){
      if(this.pData){ 
          this.formData = this.pData; 
          this.formData.gender = this.formData.gender == 0 ? '男' : '女';
          let type = this.formData.credentialType; 
          switch (type) {
              case '0':
                this.formData.credentialType = '身份证' 
                  break;
              case '1':
                  this.formData.credentialType = '护照' 
                  break;
              case '2':
                this.formData.credentialType = '学生证' 
                  break;
              case '3':
                  this.formData.credentialType = '军官证' 
                  break;
              case '4':
                this.formData.credentialType = '驾照' 
                  break;
              case '5':
                  this.formData.credentialType = '其他' 
                  break;
              default:
                  break;
          } 
          this.$http.post(baseUrl + `/vcpface/facerepository/findByPage`).then(response =>{
            this.constant.lib = response.data.res.data.map(item => (({name, id}) => ({name, id}))(item));
            this.constant.lib.forEach(item =>{
                if(item.id == this.formData.repoId){
                    this.formData.repoId = item.name;
                }
            })
          })
      }
  },
  mounted(){}
}
</script>
<style lang="scss" scoped>
.faceDetail{
    .el-form-item {
        margin-bottom: 0px;
        color: #fff!important; 
    } 
}
</style>


