import axios from '../axios'

/* 
 * 算法管理模块
 */

// 保存
export const save = (data) => {
    return axios({
        url: '/vcpalgaccess/alg/save',
        method: 'post',
        data
    })
}
// 修改
export const update = (data) => {
    return axios({
        url: '/vcpalgaccess/alg/update',
        method: 'put',
        data
    })
}
// 删除
export const batchDelete = (params) => {
    return axios({
        url: '/vcpalgaccess/alg/delete',
        method: 'delete',
		params
    })
}
// 分页查询
export const findPage = (data) => {
    return axios({
        url: '/vcpalgaccess/alg/findByPage',
        method: 'post',
        data
    })
}

// 查询单个
export const findOne = (params) => {
    return axios({
        url: '/vcpalgaccess/alg/findOne',
        method: 'get',
		params
    })
}




