<template>
    <div class="sub-page">
		<div class="zzjggl">
      <div class="panel">
          <div class="base-search">
              <label>名称：</label>
              <span class="m-input">
                  <el-input clearable v-model="dataSearch.name" placeholder="请输入关键字"></el-input>
              </span>
              <label>类型：</label>
              <span >
                  <el-select 
                  popper-class="m-select-con"
                  v-model="dataSearch.type"
                  placeholder="请选择">
                      <el-option
                      v-for="item in deviceTypeOptions "
                      :key="item.value"
                      :label="item.text"
                      :value="item.value">
                      </el-option>
                  </el-select>
              </span>
              <a href="javascript:void(0)" class="btn-search"  @click="search()" v-if="permissionResource.hasPermission('RETRIEVE')">查询</a>
              <a href="javascript:void(0)" class="btn-add" @click="add()" v-if="permissionResource.hasPermission('CREATION')">新增</a>       
              <a href="javascript:void(0)" class="btn-del fr" @click="batDel()" v-if="permissionResource.hasPermission('DELETION')">批量删除</a>
              <!--<a href="javascript:void(0)" class="btn-ok fr">批量确认</a>-->             
          </div>
          <div class="line"></div>
          <div>
              <table-grid :option="option" ref="tableGrid">
                  <template slot-scope="row">
                    <a href="javascript:void(0)" @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')" title="编辑">
                      <i class="btn-icon icon-16"></i>
                    </a>
                    <a href="javascript:void(0)" @click="del(row)" v-if="permissionResource.hasPermission('DELETION')" title="删除">
                      <i class="btn-icon icon-17"></i>
                    </a>
                    <a href="javascript:void(0)"  @click="config(row)" v-if="permissionResource.hasPermission('UPDATING')" title="配置">
                      <i class="btn-icon icon-13"></i>
                    </a>
                  </template>
              </table-grid>
          </div>
      </div>
    </div>
    <msg-dialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
      <dialog-opt :pData='msgDialog.data' @result="dialogOpt"></dialog-opt>
    </msg-dialog>
    <msg-dialog v-if="dialogVisibleConfig.msgDialog" :pDialog='msgDialogConfig' @equit="equit">
      <dialog-config :pData='msgDialogConfig.data' @result="dialogOpt" @equit="equit"></dialog-config>
    </msg-dialog>
  </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogOpt from "@/views/xinjiang/faceLibrary/opt/faceLibManager/libOpt";
import dialogConfig from "@/views/xinjiang/faceLibrary/opt/faceOpt";
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      dialogVisible: {
        msgDialog: false
      },
      msgDialog: {
        title: "新增",
        dialogVisible: true,
        width: "520px",
        data: {}
      },
      dialogVisibleConfig: {
        msgDialog: false
      },
      msgDialogConfig: {
        title: "配置",
        dialogVisible: true,
        width: "520px",
        data: {}
      },
      constant: [],
      option: {
        isShowTableNav: false,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          tableTitle: [
            { prop: "id", label: "ID" },
            { prop: "name", label: "名称" },
            { prop: "faceCount", label: "人脸数" },
            {
              prop: "type",
              label: "类型",
              format: (row, column, cellValue, index) => {
                return (
                  <div>
                    {this.deviceTypeOptions.find(e => {
                      return e.value === row.type;
                    })}
                  </div>
                );
              }
            },
            {
              prop: "createTime",
              label: "时间",
              format: (row, column, cellValue, index) => {
                return (
                  <div>
                    {this.$util.formatDateByArg(
                      row.createTime,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          tableBodyOverFlow: false,
          fixedHeight: 360,
          hasCustomColum: permissionResource.hasPermission('UPDATING')||permissionResource.hasPermission('DELETION')?true:false,
          opeColumnWidth: 220,
          prop: {
            totalcount: "res.totalRow",
            data: "res.data",
            page: "pageNum"
          }
        },
        customLabel: "操作",
        url: "/vcpface/facerepository/findByPage",
        type: "post",

        param: {},
        baseParam: {
          //请求统一参数
          pageSize: 10
        },
        notRequestSever: false
      },
      deviceTypeOptions: [
        {
          text: "全部",
          value: ""
        },
        {
          text: "黑名单",
          value: 2
        },
        {
          text: "白名单",
          value: 3
        },
        {
          text: "红名单",
          value: 4
        }
      ],
      dataSearch: {}
    };
  },
  created() {
    /*  this.getConstant(); */
  },
  methods: {
    getConstant() {
      //获取枚举数据
      this.$http.get(`/operation-log/constant`).then(response => {
        this.constant = response.data;
        this.getSearch();
      });
    },
    getSearch() {
      let newArr = this.constant.find(res => {
        //获取枚举
        return res.id == "AlarmType";
      });
      let Type = this.constant.find(res => {
        //获取枚举
        return res.id == "Type";
      });
      this.deviceTypeOptions = Type.options;
      if (!this.deviceTypeOptions) return;
      this.deviceTypeOptions.unshift({
        value: "",
        text: "全部"
      });
    },
    equit() {
      this.$refs.tableGrid.flushTableData();
      this.dialogVisible.msgDialog = false;
      this.dialogVisibleConfig.msgDialog = false;
    },
    add() {
      this.msgDialog.title = "新增";
      this.$util.clearObjectValue(this.msgDialog.data.data);
      this.msgDialog.data.methods = "post";
      this.msgDialog.data.url = "/vcpface/facerepository/save";
      this.dialogVisible.msgDialog = true;
    },
    edit(row) {
      this.msgDialog.title = "编辑";
      this.msgDialog.data.methods = "put";
      this.msgDialog.data.url = "/vcpface/facerepository/update";
      this.msgDialog.data.data = row.rowData;
      this.dialogVisible.msgDialog = true;
    },
    dialogOpt(_data) {
      if (_data) {
        this.$refs.tableGrid.flushTableData();
        this.equit();
      }
    },
    search() {
      console.log("frf", { para: this.searchForm });
      this.$refs.tableGrid.searchTableData(
        Object.assign({ para: this.dataSearch })
      );
    },
    del(row) {
      let id = row.rowData.id;
      this.delData(id);
    },

    batDel() {
      let allData = [];
      this.$refs.tableGrid.getSelectedRow().forEach(e => {
        allData.push(e.id);
      });
      if (
        Object.prototype.toString.call(allData) === "[object Array]" &&
        allData.length === 0
      ) {
        this.$message({
          message: "请选择要删除的记录",
          type: "warning"
        });
        return;
      }
      this.delData(allData.join(","));
    },
    delData(id) {
      let vm = this;
      this.$confirm("是否要删除人像库?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http
          .delete(`/vcpface/facerepository/delete`, { id: id })
          .then(response => {
            this.$message({
              message: response.data.msg || "删除成功",
              type: "success"
            });
            vm.$refs.tableGrid.flushTableData();
          });
      });
    },
    config(row) {
      this.dialogVisibleConfig.msgDialog = true;
      this.msgDialogConfig.data = {repoId:row.rowData.id,type: '新增'};
    }
  },
  components: {
    "table-grid": tableGrid,
    "msg-dialog": msgDialog,
    "dialog-opt": dialogOpt,
    "dialog-config": dialogConfig
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
  background: url("../../../assets/img/login/bg1.jpg") center 0 no-repeat
    #051121;
}
.sub-page .left-menu li {
  text-align: left;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
.search-more {
  display: inline-block;
}
.search-more .search-more-item-time {
  width: 50%;
}
</style>
<style lang="scss">
.index-content .warn-base-search {
  .el-select {
    width: 100%;
  }
  .m-input .el-input input {
    width: 100%;
  }
}
</style>


