<template>
  <div class="myplay">
      <LivePlayer 
        ref='livePlayer' 
        :video-url='playerOptions.url'
        :video-title='playerOptions.title'
        :poster='playerOptions.poster'
        :autoplay='playerOptions.autoplay'
        :loop='playerOptions.loop'
        :live='playerOptions.live'
        :alt='playerOptions.alt' 
        :muted='playerOptions.muted'
        :aspect='playerOptions.aspect'
        :loading='playerOptions.loading'
        :fluent='playerOptions.fluent'
        :stretch='playerOptions.stretch'
        :timeout='playerOptions.timeout'
        :show-custom-button='playerOptions.showButton'
        :resolution='playerOptions.resolution'
        :resolutiondefault='playerOptions.resolutiondefault'  
    >
    </LivePlayer> 
  </div>
</template>

<script>
import LivePlayer from '@liveqing/liveplayer'

export default {
  components:{LivePlayer},
  props:{
    pData: {
      type: Object,
      default: ()=>{}
    }
  },
  data(){
    return {
        playerOptions:{
            url: '', //视频流地址
            title: '视频监控', //视频右上角显示的标题
            poster: '', //视频封面图片
            autoplay: true, //自动播放
            loop: false, //是否循环播放
            live: true, //是否直播
            alt:'', //视频流地址没有指定情况下, 视频所在区域显示的文字
            muted: false, //是否静音
            aspect: 'fullscreen',//'16:9', //视频显示区域的宽高比
            loading: '', //指示加载状态
            fluent: true, //流畅模式
            stretch: false, //是否拉伸
            timeout: 20, //m3u8 加载超时(秒) Number default 20
            showButton: true, //是否在工具栏显示自定义按钮(极速/流畅, 拉伸/标准)
            resolution: 'yh,fhd,hd,sd', //供选择的清晰度配置 
            resolutiondefault: 'hd', //默认播放的清晰度 
        },
    }
  },
  watch:{},
  computed:{}, 
  created(){
    
  },
  mounted(){},
  methods:{
    //播放
    play(){
      this.$refs.LivePlayer.play();
    },
    //暂停
    pause(){
      this.$refs.LivePlayer.pause();
    },
    //暂停状态
    paused(){
      this.$refs.LivePlayer.paused();
    },
    //获取静音状态
    setMuted(){
      this.$refs.LivePlayer.setMuted();
    },
    //静音状态
    getMuted(){
      this.$refs.LivePlayer.getMuted();
    },
    //获取当前播放时间进度, 同步返回播放时间进度数据
    getCurrentTime(){
      this.$refs.LivePlayer.getCurrentTime();
    },
    //外部 API 方式获取快照, 快照获取成功后, 触发 snapOutside Event
    snap(){
      this.$refs.LivePlayer.snap();
    },
    //切换视频流方法
    toggleStream(url){ 
      this.$refs.LivePlayer.setAttribute("video-url", url);
    },
    //m3u8 加载失败时触发通知消息
    getMsg(){
      this.$refs.LivePlayer.addEventListener('message', evt => {
          console.log(`参数: { type: '', message: ''}`, evt.detail[0]);
      });
    }, 
    //播放器出错回调
    getError(){
      this.$refs.LivePlayer.addEventListener('error', evt =>{
        console.log(`参数: Error Object`, evt.detail[0]);
      })
    },
    //结束
    getEnded(){
      this.$refs.LivePlayer.addEventListener('ended', evt =>{
        console.log(`参数: 无`, evt.detail[0]);
      })
    },
    //进度更新
    getTimeupdate(){
      this.$refs.LivePlayer.addEventListener('timeupdate', evt =>{
        console.log(`参数: 当前时间进度`, evt.detail[0]);
      })
    },
    //暂停
    getPause(){
      this.$refs.LivePlayer.addEventListener('pause', evt =>{
        console.log(`参数: 当前时间进度`, evt.detail[0]);
      })
    },
    //暂停
    getPlay(){
      this.$refs.LivePlayer.addEventListener('play', evt =>{
        console.log(`参数: 当前时间进度`, evt.detail[0]);
      })
    },
    //内部快照回调, 由控制栏快照按钮触发
    getSnapInside(){
      this.$refs.LivePlayer.addEventListener('snapInside', evt => {
          console.log(`参数: 快照 Base64 数据`, evt.detail[0]);
      });
    },
    //外部快照回调
    getSnapOutside(){
      this.$refs.LivePlayer.addEventListener('snapOutside', evt => {
          console.log(`参数: 快照 Base64 数据`, evt.detail[0]);
      });
    },
    //切换
    changeUrl(url){
      this.playerOptions.url = url;
    },
  }
}
</script>
<style lang="scss" >
.myplay{
  position: relative;
}
</style>


