import axios from '../axios'

/* 
 * 厂家管理模块
 */

// 新增
export const save = (data) => {
    return axios({
        url: '/vcpalgaccess/manuf/save',
        method: 'post',
        data
    })
}
// 修改
export const update = (data) => {
    return axios({
        url: '/vcpalgaccess/manuf/update',
        method: 'put',
        data
    })
}
// 删除
export const batchDelete = (params) => {
    return axios({
        url: '/vcpalgaccess/manuf/delete',
        method: 'delete',
		params
    })
}
// 分页获取
export const findPage = (data) => {
    return axios({
        url: '/vcpalgaccess/manuf/findByPage',
        method: 'post',
        data
    })
}

// 获取单个
export const findOne = (params) => {
    return axios({
        url: '/vcpalgaccess/manuf/findOne',
        method: 'get',
		params
    })
}




