<template>
  <div class="sub-page">
    <!-- 左侧s -->
    <div class="left-tree-box">
      <h1 class="title">
        <i class="icon icon-tree"></i>区域树
      </h1>
      <ul id="browser" class="filetree">
        <synTree :option="treeOption" @clickNode="clickTreeNode" class="synTree"></synTree>
      </ul>
      <span class="b-01"></span>
      <span class="b-02"></span>
      <span class="b-03"></span>
      <span class="b-04"></span>
    </div>
    <!-- 左侧e -->
    <div class="zzjggl">
      <div class="panel">
        <div class="base-search">
          <label>设备名称：</label>
          <span class="m-input">
            <input name type="text" v-model="searchForm.name" placeholder="请输入关键字" class="w-200" />
          </span>
          <a href="javascript:void(0)" @click="search" class="btn-search">查询</a>
          <a href="javascript:void(0)" @click="openSeniorSearch" class="btn-search-more">高级查询</a>
          <a href="javascript:void(0)" @click="add" class="btn-add" v-if="permissionResource.hasPermission('CREATION')">新增</a>
          <a href="javascript:void(0)" class="btn-import" @click="importExcel"  v-if="permissionResource.hasPermission('CREATION')">导入</a>
          <a href="javascript:void(0)" class="btn-export">导出</a>
          <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')">批量删除</a>
        </div>
        <div class="search-more base-search clearfix device-searh-more" v-show="advancedSearch">
          <div class="search-more-item">
            <label>设备名称：</label>
            <span class="m-input">
              <input name type="text" v-model="searchForm.name" placeholder="请输入设备名称" />
            </span>
          </div>
          <div class="search-more-item">
            <label>设备编号：</label>
            <span class="m-input">
              <input name type="text" v-model="searchForm.deviceID" placeholder="请输入设备编号" />
            </span>
          </div>
          <div class="search-more-item">
            <label>产品类型：</label>
            <el-select v-model="searchForm.type" popper-class="m-select-con" placeholder="请选择">
              <el-option
                v-for="item in deviceTypeData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="search-more-item">
            <label>设备状态：</label>
            <el-select
              v-model="searchForm.networkState"
              popper-class="m-select-con"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceStatusData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="search-more-item">
            <label>所属组织：</label>
            <span class="m-input">
              <input name type="text" v-model="searchForm.orgnization" placeholder="请输入设备编号" />
            </span>
             
          </div>
          <a href="javascript:void(0)" class="search-more-btn" @click="seniorSearch">查询</a>
          <a
            href="javascript:void(0)"
            class="search-more-btn btn-hidden"
            @click="hideSeniorSearch"
          >隐藏</a>
        </div>
        <div class="line"></div>
        <div>
          <div class="base-table">
            <tableGrid :option="option" ref="deviceList">
              <template slot-scope="row">
                <a href="javascript:void(0)" @click="reboot(row)" title="重启">
                  <i class="btn-icon icon-12"></i>
                </a>
                <a href="javascript:void(0)" title="配置" v-if="permissionResource.hasPermission('UPDATING')">
                  <i class="btn-icon icon-13"></i>
                </a>
                <a href="javascript:void(0)" @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')" title="编辑">
                  <i class="btn-icon icon-16"></i>
                </a>
                <a href="javascript:void(0)" @click="deleteDevice(row)" v-if="permissionResource.hasPermission('DELETION')" title="删除">
                  <i class="btn-icon icon-17"></i>
                </a>
                <a href="javascript:void(0)" v-if="permissionResource.hasPermission('UPDATING')" title="口令修改">
                  <i class="btn-icon icon-14"></i>
                </a>
              </template>
            </tableGrid>
          </div>
        </div>
      </div>
    </div>
    <addDialog :pDialog="addOption" @equit="closeDialog">
      
      <el-form
        :inline="true"
        :model="device"
        label-width="100px"
        :rules="deviceValidata"
        class="deviceForm"
        ref="deviceForm"
      >
      <p class="device-title">基本信息</p>
        
        <el-form-item label="设备名称:" prop="name">
          <el-input
            placeholder="请输入设备名称"
            :readonly="isCheck"
            v-model="device.name"
            :clearable="true"
          ></el-input>
        </el-form-item>


        <!-- <el-form-item label="使用场景:" >
          <el-select v-model="device.sceneCode" popper-class="m-select-con" placeholder="请选择" class="device-scene-root">
              <el-option
                v-for="item in sceneCodeData"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>  
        </el-form-item>  -->


        <el-form-item label="设备品牌:" prop="manufacturerName">
          <el-select
              v-model="device.manufacturerName"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in brandData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="接入服务器:" prop="serverObject.serverId">
          <el-select
              v-model="device.serverObject.serverId"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in serverObjectData"
                :key="item.serverId"
                :label="item.name"
                :value="item.serverId"
              ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="服务器地址:" >
          <el-input
            v-model="serverObject.address"
            :readonly="true"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label="服务器编码:" >
          <el-input
            v-model="serverObject.serverId"
            :readonly="true"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label="服务器类型:" >
          <el-input
            v-model="serverObject.serverType.text"
            :readonly="true"
            :clearable="true"
          ></el-input>
        </el-form-item>
        
        
        <el-form-item label="登录用户:" prop="username">
          <el-input
            placeholder="请输入登录用户"
            v-model="device.username"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="登录密码:" prop="password">
          <el-input
            placeholder="请输入登录密码"
            v-model="device.password"
            :readonly="isCheck"
            :clearable="true"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item label="所属组织:" prop="organizationCode">
          <dropDownTree ref="dropDownTree" v-model="device.organizationCode" :value="dropDownTree.value" :prop="dropDownTree.prop" :treeOption="dropDownTree.treeOption" ></dropDownTree>
        </el-form-item>
        <el-form-item label="联系方式:" prop="phone">
          <el-input
            placeholder="请输入联系方式"
            v-model="device.phone"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="责任人:" prop="owner">
          <el-input
            placeholder="请输入责任人"
            v-model="device.owner"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="IP地址:" prop="ip">
          <el-input placeholder="请输入IP地址" v-model="device.ip" :readonly="isCheck" :clearable="true"></el-input>
        </el-form-item>

        <el-form-item label="PORT:" prop="port">
          <el-input
            placeholder="请输入PORT"
            v-model="device.port"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="MAC地址:" prop="mac">
          <el-input
            placeholder="请输入MAC地址"
            v-model="device.mac"
            :readonly="isCheck  || !addOption.isAdd"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="标准地址:" prop="location">
          <el-cascader
            :options="areaData"
            :props="areaDataProp"
            v-model="device.location"
            @change="selectArea"
            popper-class="m-cascader"
          ></el-cascader>
        </el-form-item> -->
        <el-form-item label="地址详细:" prop="locationInfo">
          <el-input
            placeholder="请输入地址详细"
            v-model="device.locationInfo"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="经度:" prop="coordinateX">
          <el-input
            placeholder="请输入经度"
            v-model="device.coordinateX"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="纬度:" prop="coordinateY">
          <el-input
            placeholder="请输入纬度"
            v-model="device.coordinateY"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="备注:">
          <el-input placeholder="请输入备注" v-model="device.note" :readonly="isCheck" :clearable="true"></el-input>
        </el-form-item>
        <el-form-item label="rtsp:">
          <el-input
            placeholder="请输入rtsp"
            v-model="device.rtsp"
            :readonly="isCheck"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <p class="device-title">设备编号</p>

        <el-form-item label="省份:">
          <el-select
              v-model="deviceInfo.province"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceInfo.provinceData"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
        </el-form-item>


        <el-form-item label="城市:">
          <el-select
              v-model="deviceInfo.city"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceInfo.cityData"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="区县:" >
          <el-select
              v-model="deviceInfo.county"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceInfo.countyData"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="单位:">
          <el-input
            placeholder="请输入单位"
            v-model="deviceInfo.unit"
            :readonly="isCheck || !addOption.isAdd"
            :clearable="true"
          ></el-input>
        </el-form-item>


        <el-form-item label="行业类型:">
          <el-select
              v-model="deviceInfo.industrCategory"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceInfo.industrCategoryData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
        </el-form-item>


        <el-form-item label="设备类型:" >
          <el-select
              v-model="deviceInfo.equipmentType"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceInfo.equipmentTypeData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
        </el-form-item>


        <el-form-item label="网络标识:" >
          <el-select
              v-model="deviceInfo.networkIdentification"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder="请选择"
            >
              <el-option
                v-for="item in deviceInfo.networkIdentificationData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
        </el-form-item>

        
        <el-form-item label="设备序号:">
          <el-input
            placeholder="请输入设备序号"
            v-model="deviceInfo.equipmentSerialNumber"
            :readonly="true"
            class="device-button-input"
          ></el-input>
          <el-button type="primary" class="device-input-button" @click="getEquipmentSerialNumber">刷新</el-button>
        </el-form-item>


        <el-form-item label="设备编号:" prop="deviceID">
          <el-input
            placeholder="请输入设备编号"
            v-model="getDviceId"
            :readonly="true"
          ></el-input>
        </el-form-item>

        
      </el-form>
      <div class="search-more deviceAddOpt">
        <a href="javascript:void(0)" class="search-more-btn" @click="addOrEdit">确定</a>
        <a href="javascript:void(0)" class="search-more-btn" @click="cancel">取消</a>
      </div>
    </addDialog>

    <fileUpload :option="fileUploadOption" ref="importFileDialog"></fileUpload>

    
  </div>
</template>

<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import synTree from "@/views/xinjiang/common/components/tree/synTree";
import addDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import fileUpload from "@/views/xinjiang/common/components/fileUpload/importFile"
import DeviceInfo from "./model/DeviceInfo";
import dropDownTree from "@/views/xinjiang/common/components/dropDownTree/dropDownTree"
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      requestServer : false,
      advancedSearch: false,
      option: {
        isShowTableNav: false,
        tableOption: {
          isShowSelect: true,
          singleSelect: false,
          tableTitle: [
            { prop: "deviceID", label: "设备编号" },
            { prop: "name", label: "设备名称" },
            { prop: "ip", label: "IP" },
            { prop: "port", label: "PORT" },
            { prop: "networkState", label: "状态" },
            {
              prop: "mac",
              label: "MAC地址"
            },
            {
              prop: "modifiedDate",
              label: "更新时间",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.modifiedDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          tableBodyOverFlow: false,
          fixedHeight: 360,
          hasCustomColum: true,
          opeColumnWidth: 220
        },
        customLabel: "操作",
        url: "/regisger-object",
        type: "get",
        notRequestSever: false,
        param: {}
      },

      treeOption: {
        url: "/organization/user-org-tree",
        param: {},
        prop: {
          children: "children",
          label: "name"
        },
        type: "get"
      },

      addOption: {
        title: "添加设备",
        dialogVisible: false,
        width: "70%",
        isAdd: true,
        id: ""
      },

      device: {
        area: {
          areaCode: ""
        },
        brand: "",
        coordinateX: "",
        coordinateY: "",
        deviceID: "",
        ip: "",
        location: "",
        locationInfo: "",
        mac: "",
        name: "",
        note: "",
        organizationCode: "",
        owner: "",
        password: "",
        phone: "",
        port: "",
        type: "",
        rtsp: "",
        username: "",
        sceneCode: "",
        serverObject: {
          serverId : ""
        },
        serverId : "",
      },
      serverObject : {
        serverId : "",
        address : "",
        serverType : {
          text : ""
        }
      },
      deviceInfo:new DeviceInfo(),
      deviceValidata: {
        name: [{ required: true, message: "请输入" }],
        ip: [{ required: true, message: "请输入" }],
        coordinateX: [{ required: true, message: "请输入" }],
        coordinateY: [{ required: true, message: "请输入" }],
        port: [{ required: true, message: "请输入" }],
        username: [{ required: true, message: "请输入" }],
        deviceID: [{ required: true, message: "请输入" }],
        password: [{ required: true, message: "请输入" }],
        location: [{ required: true, message: "请填写标准地址" }],
        mac: [{ required: true, message: "请填写mac地址" }],
        phone: [{ required: true, message: "请填写联系电话" }],
        owner: [{ required: true, message: "请填写责任人" }],
        locationInfo: [{ required: true, message: "请填写详细地址" }],
        organizationCode: [{ required: true, message: "请填写所属组织" }],
        brand : [{ required: true, message: "请输入品牌" }],
      },
      isCheck: false,
      areaData: [],
      sceneCodeData:[],
      serverObjectData : [],
      brandData : [],
      areaDataProp: {
        label: "name",
        value: "id",
        children: "children"
      },

      searchForm: {
        name: "",
        deviceID: "",
        type: "",
        networkState: "",
        organizationCode: ""
      },
      deviceTypeData: [],
      deviceStatusData: [],
      treeNode: {},
      treeAreaInfo:"",
      fileUploadOption:[{
        url : '/regisger-object/import/excel',//上传路径
        name : 'file', // 文件key
        type : ["xls","xlsx"],// 指定限制文件名
        size : 1024,// 指定大小
        label : "导入设备"
      }],
      dropDownTree:{
        prop:{
          name : "name",
          value : "orgCode"
        },
        value : "",
        treeOption:{
          url: "/organization/only-org-tree",
          param: {},
          prop: {
            children: "children",
            label: "name"
          },
          type: "get"
        }
      }
    };
  },

  mounted() {
    this.getAreaData();
    this.getConstant();
    //this.getSceneCodeData();
  },

  methods: {
    search() {
      let param = {};
      if (this.treeNode.id) {
        param.areaCode = this.treeNode.id;
      }
      if (this.searchForm.name) {
        param.name = this.searchForm.name;
      }
      //Object.assign(param,this.searchForm);
      this.$refs.deviceList.searchTableData(param);
    },

    seniorSearch() {
      let param = {};
      if (this.treeNode.id) {
        param.areaId = this.treeNode.id;
      }
      Object.assign(param, this.searchForm);
      this.$refs.deviceList.searchTableData(param);
    },

    getSceneCodeData(){
      this.$http.get('/vcpsystem/dict/findByType?type=scene').then(response =>{
        this.sceneCodeData = response.data.data || [];
      })
    },

    getAreaData() {
      this.$http.get("/area/area-tree").then(response => {
        this.treeAreaInfo = JSON.parse(JSON.stringify(response.data));
        this.areaData = this.$util.listToTree(response.data, {
          code: "id",
          parent: "parentId",
          children: "children"
        });
      });
    },

    getConstant() {
      this.$http.get("/regisger-object/constant").then(response => {
        let data = {
          text:"请选择",
          value : "",
        }
        response.data[0].options.unshift(data);
        response.data[1].options.unshift(data);
        response.data[2].options.unshift(data);
        this.deviceStatusData = response.data[0].options;
        this.deviceTypeData = response.data[1].options;
        this.brandData = response.data[2].options;
        this.deviceInfo.industrCategoryData = response.data[3].options;
        this.deviceInfo.equipmentTypeData = response.data[4].options;
        this.deviceInfo.networkIdentificationData = response.data[5].options;
        
      });
    },

    add() {
      this.addOption.title = "添加设备";
      this.addOption.isAdd = true;
      
      this.clear();
      this.addOption.dialogVisible = true;
    },

    edit(row) {
      //his.$util.clearObjectValue(this.device);
      this.clear();
      this.getEditData(row.rowData);
      this.addOption.title = "编辑设备";
      this.addOption.isAdd = false;
      this.addOption.id = row.rowData.id;
      
      this.addOption.dialogVisible = true;
    },

    getEditData(deviceData) {
      this.$util.setObjectValue(this.device, deviceData);
      if(this.device.serverObject){
        this.device.serverId = this.device.serverObject.serverId;
      }
      this.device.brand = this.device.brand.value;
      this.deviceInfo.setData(this.device.deviceID);
      // let locationNodes = this.$util.getParents(this.treeAreaInfo,{
      //   prop: {
      //     id: "id",
      //     parentId: "parentId"
      //   },
      //   searchId : this.device.location
      // });
      // this.device.location = [];
      // for(let i = 0 ; i < locationNodes.length ; i++){
      //   this.device.location.push(locationNodes[i].id);
      // }
    },

    addOrEdit() {
      if(this.requestServer){
        console.log("该请求正在执行中");
        return ;
      }
      this.$refs.deviceForm.validate(validate => {
        if (validate) {
          if (this.device.location && this.device.location.length) {
            this.device.area.areaCode = this.device.location[
              this.device.location.length - 1
            ];
            this.device.location = this.device.area.areaCode;
          } else {
            this.device.area.areaCode = "";
            this.device.area.location = "";
          }
          this.requestServer = true;
          if (this.addOption.isAdd) {
            this.$http.post("/regisger-object", this.device).then(response => {
              this.addOption.dialogVisible = false;
              this.$message({
                message: "设备添加成功",
                type: "success"
              });
              this.requestServer = false;
              this.$refs.deviceList.flushTableData();
            });
          } else {
            this.$http
              .put(`/regisger-object/${this.addOption.id}`, this.device)
              .then(response => {
                this.addOption.dialogVisible = false;
                this.$message({
                  message: "编辑设备成功",
                  type: "success"
                });
                this.requestServer = false;
                this.$refs.deviceList.flushTableData();
              });
          }
        } else {
          console.log("字段校验不通过");
          return false;
        }
      });
    },

    cancel() {
      this.addOption.dialogVisible = false;
      this.requestServer = false;
    },

    closeDialog(){
      this.addOption.dialogVisible = false;
      this.requestServer = false;
    },

    openSeniorSearch() {
      this.advancedSearch = !this.advancedSearch;
    },

    hideSeniorSearch() {
      this.advancedSearch = false;
    },

    selectArea() {},

    clickTreeNode(data, node) {
      let param = {
        areaCode: data.areaCode,
        orgCode:data.orgCode
      };
      this.treeNode = data;
      this.$util.clearObjectValue(this.searchForm);
      this.$refs.deviceList.searchTableData(param);
    },

    reboot(row) {
      let id = row.rowData.deviceID;
      this.$http.post(`/regisger-object/device-reboot/${id}`).then(response => {
        this.$message({
          message: "设备重启成功",
          type: "success"
        });
      });
    },

    deleteDevice(row) {
      let id = row.rowData.id;
      this.$confirm("是否要删除改设备?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http.delete(`/regisger-object/${id}`).then(response => {
          this.$message({
            message: "删除成功",
            type: "success"
          });
          this.$refs.deviceList.flushTableData();
        });
      });
    },
    importExcel(){
      this.$refs.importFileDialog.openView();
    },
    getEquipmentSerialNumber(){
      this.deviceInfo.getEquipmentSerialNumber();
    },

    clear(){
      if(this.$refs.dropDownTree){
        this.$refs.dropDownTree.clear();
      }
      this.deviceInfo.province = "";
      this.deviceInfo.city = "";
      this.deviceInfo.industrCategory= "";
      this.deviceInfo.equipmentType = "";
      this.deviceInfo.networkIdentification = "";
      this.deviceInfo.equipmentSerialNumber = "";
      this.$util.clearObjectValue(this.device);
    },
  },

  watch: {
    'device.serverObject.serverId' : function(oldValue,newValue){
      if(!this.device.serverObject.serverId){
        this.$util.clearObjectValue(this.serverObject);
        return ;
      }
      for(let i = 0 ; i < this.serverObjectData.length ; i++){
        if(this.device.serverObject.serverId == this.serverObjectData[i].serverId){
          this.$util.setObjectValue(this.serverObject , this.serverObjectData[i]);
          this.device.serverId = this.serverObject.serverId;//后端需要此字段
        }
      }
    },

    "device.manufacturerName" : function(oldValue , newValue) {
      this.serverObjectData = [];
      if(!this.device.manufacturerName){
        return ;
      }
      this.$http.get(`/regisger-object/server-tree/${this.device.manufacturerName}`).then(res => {
        this.serverObjectData = res.data || [];
      })
    }
  },

  computed: {
    getDviceId:{
      get : function(){
        let deviceId = "";
        let deviceInfo = this.deviceInfo;
        let industrCategory = deviceInfo.industrCategory || "00";
        let equipmentType = deviceInfo.equipmentType || "000";
        let networkIdentification = deviceInfo.networkIdentification || "0";
        deviceId = deviceInfo.getProvinCode() + deviceInfo.getCityCode() + deviceInfo.getCountyCode() + deviceInfo.unit 
          + industrCategory + equipmentType + networkIdentification + deviceInfo.equipmentSerialNumber;
        this.device.deviceID = deviceId;
        return deviceId;
      },
      set : function(value){
        this.device.deviceID = value;
      }
    }
  },

  components: {
    tableGrid,
    synTree,
    addDialog,
    fileUpload,
    dropDownTree
  }
};
</script>
<style lang="scss" scoped>
.sub-page-content {
  background: url("../../../assets/img/login/bg1.jpg") center 0 no-repeat
    #051121;
}
</style>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.search-more {
  display: block;
}
.deviceAddOpt {
  border: unset;
  text-align: center;
}

.deviceForm .el-form-item {
  margin-bottom: 20px;
}
.base-search {
  text-align: left;
  padding-bottom: 10px;
}
.serviceSelect{
  width: 216px;
}
.device-title{
  color: #fff;
  font-size: 18px;
  margin-bottom: 20px;
}
.device-button-input{
  width: 67%;
}

.device-input-button{
  width: 30%;
  height: 30px;
}
</style>




